.text{
    margin-top: -550px;
    overflow: hidden;
    position: relative;
    z-index: 3;
    &__content{
        padding-top: 260px;
        @include windowMax1440p {
            padding-top: 200px;
        }
        @include windowMax1280p{
            padding-top: 160px;
        }
        @include mobileMax952p{
            padding-top: 120px;
        }
        &__divider{
            
            width: calc(100% + (100vw - 1330px)/2);
            display: flex;
            align-items: center;
            gap: 32px;
            p{
                @include poppins16();
                letter-spacing: 0.1em;
                color: var(--white-color);
                opacity: 0.4;
                text-transform: uppercase;
                @include windowMax1440p {
                    @include poppins12();
                }
                @include windowMax1280p{
                    @include poppins10();
                }
                @include mobileMax952p{
                    @include poppins14();
                }
            }
            &__line{
                height: 1px;
                width: 100%;
                background-color: var(--white-color);
                opacity: 0.16;
                flex-shrink: 0;
            }
        }
        h2{
            @include poppins56();
            padding-top: 48px;
            letter-spacing: 0.02em;
            color:rgba($color: #ffffff, $alpha: 0.2);
            //opacity: 0.2;   
            max-width: 850px;
            @include windowMax1440p {
                @include poppins40();
                max-width: 569px;
            }
            @include windowMax1280p{
                @include poppins32();
            }
            @include mobileMax952p{
                @include poppins28();
                padding-top: 32px;
            }
        }
        &__block{
            @include poppins16_300();
            letter-spacing: 0.04em;
            color: var(--white-color);
            opacity: 0.8;
            padding-top: 56px;
            padding-left: 72px;
            max-width: 912px;
            @include windowMax1440p {
                @include poppins14_300();
                padding-left: 77px;
                max-width: 744px;
            }
            @include windowMax1280p{
                @include poppins12_300();
                padding-left: 58px;
                max-width: 738px;
            }
            @include mobileMax952p{
                @include poppins14_300();
                padding-left: 0px;
                padding-top: 24px;
            }
        }
        &__numbers{
            display: flex; 
            gap: 215px;
            padding-top: 140px;
            padding-left: 72px;
            @include windowMax1440p {
                padding-top: 96px;
                padding-left: 77px;
                gap: 164px;
            }
            @include windowMax1280p{
                padding-top: 88px;
                padding-left: 58px;
                gap: 151px;
            }
            @include mobileMax952p{
                padding-top: 64px;
                padding-left: 0px;
                padding-bottom: 104px;
                gap: 73px;
                flex-wrap: wrap;
                //justify-content: center;
            }
            @include tablet768-952p{
                gap: 28px;
            }
            &__double{
                display: flex;
                gap: 215px;
                @include windowMax1440p {
                    gap: 164px;
                }
                @include windowMax1280p{
                    gap: 151px;
                }
                @include mobileMax952p{
                    gap: 73px;
                }
                @include tablet768-952p{
                    gap: 28px;
                }
                &__item{
                    display: flex;
                    flex-direction: column;
                    &__circle{
                        border-radius: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 72px;
                        height: 72px;
                        border: solid 0.75px rgba($color: #ffffff, $alpha: 0.3);
                    }
                    p{
                        @include poppins16_300();
                        letter-spacing: 0.04em;
                        opacity: 0.8;
                        color: var(--white-color);
                        padding-top: 24px;
                        max-width: 200px;
                        @include windowMax1440p {
                            @include poppins14_300();
                            padding-top: 8px;
                        }
                        @include windowMax1280p{
                            @include poppins12_300();
                            
                        }
                        @include mobileMax952p{
                            @include poppins14_300();
                            padding-top: 8px;
                            width: 117px;
                        }
                    }   
                }
            }
        }
    }
}