.split-line-white{
    background: linear-gradient(90deg, var(--white-color) 50%, transparent 50%);
    background-size: 200%;
    background-position-x: 100%;
    background-clip: text;
}

.split-line-iris{
    background: linear-gradient(90deg, var(--iris-color) 50%, transparent 50%);
    background-size: 200%;
    background-position-x: 100%;
    background-clip: text;
}